import React from "react"
import { Link } from "gatsby"



const Card = ({cardData}) => {

  return (
    <>
    {cardData.map((e,i) => {
        return(
        <div className="card-box" key={i}>
            <Link to="">
                <div className="card-cover">
                    <img src={e.cover} alt="ring" />
                </div>
                <div className="card-info">
                    <h4>{e.heading}</h4>
                    <ul>
                        {e.category.map((d,j) => {
                            return(
                                <li key={j}>{d}</li>   
                            )
                        })}
                    </ul>
                </div>
            </Link>
        </div>
        )
        })}
    </>
  )
}

export default Card