import React from "react"
import {Helmet} from 'react-helmet'
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css';
import Footer from "../footer/footer"
import Header from "../header/header"
import SubHeader from "../header/subheader";
import MobileHeader from "../header/mobileHeader";

const Layout = ({children}) => {
  const getHeader = () => {
    if(typeof window !== 'undefined' && window.location.pathname === '/'){
      return <><MobileHeader /><Header /><SubHeader /></>
    }
    return ''
  }
  return (
    <div className="wrapper">
      <Helmet>
          <meta name='description' content='Included by design is a showcase of Case Studies illustrating what inclusive, fair, accessible, ethical, and responsible design looks like in various products - from designs to advertising campaigns and for various industries and identities.' />
      </Helmet>
      <div className="header">
      {getHeader()}
      </div>
      <div className="content">
        {children}
      </div>
      <Footer />
    </div>
  )
}

export default Layout