import React from "react"
import { Link } from "gatsby"
import back from "../../images/footer/arrow.svg"
import "./style.css";

const Footer = () => {

  const scrollToTop = () =>{
    window.scrollTo({
      top: 0, 
      behavior: 'smooth'
    });
  };

  return (
    <div className="footer">
      
        <div className="footer-menu">
          <div className="container">
            <span className="footer-logo" />
            <p>We collect examples to help creatives make more equitable products and services for all</p>
            <ul>
              <li><Link to="">Launch app</Link></li>
              <li><Link to="">About Us</Link></li>
              <li><Link to="">Contact Us</Link></li>
              <li><Link to="">Accessibility</Link></li>
            </ul>
          </div>
        </div>

        <div className="bottom-footer">
          <div className="container">
            <div className="bottom-footer-grid">
              <p><span>All images copyright their owners.</span> <span>Legal stuff</span></p>
              <span className="back-to-top" role="presentation" onClick={scrollToTop}>Back to top <img src={back} alt="" /></span>
            </div>
          </div>
        </div>
        
     
    </div>
  )
}

export default Footer