import React from "react"
import Dropdown from 'react-bootstrap/Dropdown'
import categoryIcon from "../../images/header/category-icon.svg"
import industryIcon from "../../images/header/industry-icon.svg"
import identitiesIcon from "../../images/header/identities-icon.svg"


const SubHeader = () => {

const data = [
    {
        icon: categoryIcon,
        menu: "Category",
        subMenu : ["Pinterest", "Product Design", "Illustration"]  
    },
    {
        icon: industryIcon,
        menu: "Industry",
        subMenu : ["Pinterest", "Product Design", "Illustration"]  
    },
    {
        icon: identitiesIcon,
        menu: "Identities",
        subMenu : ["Pinterest", "Product Design", "Illustration"]  
    },
]

  return (
    <div className="sub-header">
      <div className="container">
        <div className="sub-header-grid">

          <div className="category-grid">
            <div className="category-menu">
            {data.map((e,i) => {
            return(
                <Dropdown key={i}>
                    <Dropdown.Toggle>
                    <img src={e.icon} alt="" />{e.menu}
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                      {e.subMenu.map((d,k) => {
                        return(
                        <Dropdown.Item href="/" key={k}>{d}</Dropdown.Item>
                        )
                    })}
                    </Dropdown.Menu>
                </Dropdown>
                 )
            })}
            </div>  
          </div>

          <div className="sort-grid">
            <Dropdown>
                <Dropdown.Toggle>
                   Sort by
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item href="/">Action</Dropdown.Item>
                    <Dropdown.Item href="/">Another action</Dropdown.Item>
                    <Dropdown.Item href="/">Something else</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
          </div>

        </div>
      </div>
    </div>
  )
}

export default SubHeader