import React, { useState } from "react"
import { Button, Form } from "react-bootstrap"
import searchicon from "../../images/header/search.svg"


const MobileHeader = () => {

  const [show, setShow] = useState(false)

  const handleSearch = () => {
    setShow(!show)
  }

  return (
    <div className="mobile-header">
        <div className="container">
            <div className="mobile-header-grid">

                <div className="mobile-about-grid" />

                <div className="mobile-logo-grid" />

                <div className="mobile-search-grid">
                    <Button onClick={handleSearch}><img src={searchicon} alt="" /></Button>
                </div>

            </div>
            <div className={`mobile-search-grid-content ${show ? "active" : ""}`}>
                <Form>
                    <Form.Control
                        aria-label="Search"
                        aria-describedby="Search"
                        placeholder="Search"
                    />
                </Form>
            </div>
        </div>
    </div>
  )
}

export default MobileHeader